import arrCopy from './arrCopy.js';
import hasOwnProperty from './hasOwnProperty.js';

function shallowCopy(from) {
  if (Array.isArray(from)) {
    return arrCopy(from);
  }
  var to = {};
  for (var key in from) {
    if (hasOwnProperty.call(from, key)) {
      to[key] = from[key];
    }
  }
  return to;
}

export { shallowCopy as default };
